import React, { useState, useEffect } from 'react'
import PeerService from './PeerService'

let peerServiceSender

function App(props) {
  const urlParams = new URLSearchParams(props.location.search)
  const idParam = urlParams.get('id')
  console.log('id query parameters: ', idParam)

  const [value, setValue] = useState(idParam)
  
  useEffect(() => {
    peerServiceSender = new PeerService(idParam ? `${idParam}-sender` : 'sender-xxx-pacorampas')
  }, [])

  const handleValueChange = e => {
    setValue(e.target.value)
  }

  const handleClickConnect = () => {
    peerServiceSender.connectWithPeer(value)
    peerServiceSender.callToPeer(value)
  }

  return (
    <div>
      <h1>I am a sender</h1>
      <input onChange={handleValueChange} value={value} />
      <button onClick={handleClickConnect}>Connect</button>
    </div>
  );
}

export default App